a {
  text-decoration: none;
  color: inherit;
}

h1,
h2,
h3,
p,
ul,
li,
a {
  margin: 0;
  border-width: 0;
  padding: 0;
}
img {
  display: block;
  max-width: 100%;
  height: auto;
}
ul {
  list-style: none;
}

button {
  padding: 0;
  margin: 0;
}

button {
  cursor: pointer;
}

body {
  background-color: $primary-background-color;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  // @include page-titles-style;
}
