.modal-ingredient-opened {
  position: relative;
  overflow: hidden;
  height: 100vh;
}

.modal-ingredient {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) scale(0);
  transition: $timing-function;
  border-radius: 4px;
  z-index: 300;

  max-height: 100vh;
  overflow-y: auto;

  background-color: white;

  @media screen and (max-width: $tablet) {
    width: 320px;
    min-height: 673px;
    padding: 40px 20px;
  }

  @media screen and (min-width: $tablet) {
    width: $tablet;
    min-height: 537px;
    padding: 40px 28px;
  }
}

.modal-ingredient.active {
  transform: translate(-50%, -50%) scale(1);
}

#overlay-ingredient {
  position: fixed;
  opacity: 0;
  transition: 200ms ease-in-out;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.3);
  pointer-events: none;
}

#overlay-ingredient.active {
  opacity: 1;
  pointer-events: all;
  z-index: 2;
}

.modal__ingredient-name {
  @include title-products-style;
  @include font-size(900, 32px, 1.19, 0.02em);
  margin-bottom: 20px;

  @media screen and (min-width: $tablet) {
    margin-bottom: 28px;
  }
}

.modal__ingredient-type {
  @include page-titles-style;
  @include font-size(600, 16px, 1.6, 0.02em);

  margin-bottom: 20px;

  @media screen and (min-width: $tablet) {
    margin-bottom: 40px;
  }
}

.modal__ingredient-description {
  @include page-titles-style;
  @include font-size(400, 16px, 1.8, 0.02em);
  color: $secondary-background-color;

  @media screen and (max-width: $tablet) {
    height: 300px;
    overflow-y: scroll;
    margin-bottom: 28px;
  }

  @media screen and (min-width: $tablet) {
    height: 200px;
    overflow-y: scroll;
    margin-bottom: 28px;
  }
}

// .modal__ingredient-description::-webkit-scrollbar{
//     width: 10;
//     height: 10;
// }

.modal__ingredient-info {
  @include page-titles-style;
  @include font-size(400, 16px, 1.6, 0.02em);
  color: $secondary-background-color;

  @media screen and (max-width: $tablet) {
    margin-bottom: 32px;
  }

  @media screen and (min-width: $tablet) {
    margin-bottom: 40px;
  }
}

.modal__ingredients-hr {
  @media screen and (max-width: $tablet) {
    display: none;
  }

  @media screen and (min-width: $tablet) {
    margin-left: -28px;
    margin-right: -28px;
    margin-bottom: 28px;
    border-top: 1px solid $primary-color;
  }
}

.modal-ingredient__add-btn {
  margin-left: auto;
  width: auto;
}
