@media screen and (max-width: 767.98px) {
  .navigation, .logo-tablet, .search-form, .switcher {
    display: none;
  }

  .header-box {
    justify-content: space-between;
    align-items: center;
    padding-bottom: 20px;
    display: flex;
  }

  .burger {
    background-color: rgba(0, 0, 0, 0);
    border: none;
  }

  .close-btn {
    position: absolute;
    top: 10px;
    right: 15px;
  }

  .mobile-menu {
    z-index: 3;
    width: 100%;
    height: 100%;
    opacity: 0;
    visibility: hidden;
    pointer-events: none;
    background-color: #fff;
    transition: all .4s;
    position: fixed;
    top: 0;
    left: 0;
    -webkit-transform: translateX(-100%);
    transform: translateX(-100%);
  }

  .mobile-menu.is-open {
    opacity: 1;
    visibility: visible;
    pointer-events: auto;
    -webkit-transform: translateX(0);
    transform: translateX(0);
  }

  .mobile-menu__container {
    align-items: left;
    height: 100%;
    width: 100%;
    margin-top: 91px;
    padding-left: 20px;
    padding-right: 20px;
  }

  .mobile-menu__list {
    flex-direction: column;
    justify-content: center;
    gap: 32px;
    margin-top: 40px;
    display: flex;
  }

  .mobile-menu__link {
    letter-spacing: .02em;
    font-family: Poppins, sans-serif;
    font-size: 28px;
    font-weight: 400;
    line-height: .86;
  }

  .mobile-menu__input {
    width: 100%;
    height: 40px;
    border: 1px solid #fd5103;
    border-radius: 5px;
  }
}

