.gallery__wrap {
  padding-bottom: 60px;

  @media screen and (min-width: $tablet) {
    padding-top: 60px;
  }
}

.gallery__title {
  margin-top: 0;
  margin-bottom: 60px;
  @include font-size(600, 22px, 1.91, 0.02em);
  text-align: center;
  color: $text-color;
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);

  @media screen and (min-width: $tablet) {
    @include font-size(700, 32px, 1.31, 0.02em);
  }

  @media screen and (min-width: $desktop) {
    @include font-size(700, 48px, 1.5, 0.02em);
  }
}

.gallery__list {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 20px;
  text-align: center;

  @media screen and (min-width: $tablet) {
    gap: 32px;
  }

  @media screen and (min-width: $desktop) {
    margin-left: -17px;
    margin-right: -17px;
  }
}

.gallery__item {
  width: 280px;
  border: 1px solid $primary-color;
  border-radius: 4px;

  @media screen and (min-width: $tablet) {
    width: 335px;
  }

  @media screen and (min-width: $desktop) {
    width: 395px;
  }
}

.gallery__img {
  border-radius: 4px 4px 0px 0px;

  @media screen and (min-width: $tablet) {
    width: 335px;
  }

  @media screen and (min-width: $desktop) {
    width: 395px;
  }
}

.gallery__thumb {
  padding-top: 48px;
  padding-bottom: 24px;

  @media screen and (min-width: $tablet) {
    padding-top: 20px;
    padding-bottom: 20px;
  }

  @media screen and (min-width: $desktop) {
    padding-bottom: 28px;
  }
}

.gallery__text {
  margin-bottom: 16px;
  height: 75px;
  @include font-size(900, 26px, 1.62, 0.02em);
  text-align: center;
  color: $text-color;
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);

  @media screen and (min-width: $tablet) {
    margin-bottom: 25px;
    @include font-size(900, 28px, 1.18, 0.02em);
  }

  @media screen and (min-width: $desktop) {
    margin-bottom: 27px;
    @include font-size(900, 32px, 1.18, 0.02em);
  }
}

.btn {
  display: inline-block;
  width: 108px;
  height: 40px;
  @include font-size(400, 14px, 1.71, 0.02em);
}

.gallery__info-btn {
  margin-right: 16px;
  background: $primary-color-hover;
  border-radius: 4px;
  text-align: center;
  color: $primary-background-color;
  border: 2px solid $primary-color;
  filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
  transition: background 250ms $timing-function;

  @media screen and (min-width: $tablet) {
    margin-right: 12px;
    width: 160px;
    height: 48px;
    @include font-size(400, 20px, 1.2, 0.02em);
  }

  @media screen and (min-width: $desktop) {
    margin-right: 16px;
    width: 182px;
    @include font-size(400, 20px, 1.2, 0.02em);
  }
}

.gallery__info-btn:hover,
.gallery__info-btn:focus {
  color: $primary-color;
  background: #f5f5f5;
}

.gallery__info-btn:active {
  background: $primary-color;
  color: $primary-background-color;
}

.gallery__add-btn {
  position: relative;
  background-color: $primary-background-color;
  border-radius: 4px;
  color: $primary-color;
  border: 2px solid $primary-color;
  filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
  transition: background-color 250ms $timing-function;
  @include title-products-style;
  @media screen and (min-width: $tablet) {
    width: 122px;
    height: 48px;
    @include font-size(400, 20px, 1.2, 0.02em);
  }

  @media screen and (min-width: $desktop) {
    width: 141px;
    @include font-size(400, 20px, 1.2, 0.02em);
  }
}

.gallery__add-btn:hover,
.gallery__add-btn:focus {
  background: #f5f5f5;
}

.gallery__add-btn:active {
  background: $primary-background-color;
  border: 1px solid $primary-color;
}

.add-btn__desr {
  margin-right: 24px;
}

.add-btn__icon {
  position: absolute;
  top: 10px;
  left: 72px;

  @media screen and (min-width: $tablet) {
    left: 87px;
    width: 24px;
    height: 24px;
  }

  @media screen and (min-width: $desktop) {
    left: 98px;
  }

  fill: $primary-background-color;
}

.icon__fill {
  fill: $primary-color;
}

.nofound__img {
  min-width: 280px;
  max-width: 380px;
}

.pagination {
  display: flex;
  justify-content: center;
  align-items: center;

  gap: 10px;
  margin-top: 30px;
  @include pagination-num-style;
  @include font-size(700, 16px, 1.5);
}
.numPage {
  display: flex;
  width: 50px;
  height: 50px;

  border-radius: 50%;
  border: 3px solid $primary-color;
  justify-content: center;
  align-items: center;
  &.active {
    background-color: $primary-color;
    color: $primary-background-color;
  }
  &:hover,
  &:focus {
    transform: scale(1.1);
  }
  transition: transform 250ms linear;
  cursor: pointer;
}
.decr-arrow,
.incr-arrow {
  display: flex;
  width: 20px;
  height: 20px;
  justify-content: center;
  border-top: 4px solid $primary-color;
  border-right: 4px solid $primary-color;
  cursor: pointer;
}
.decr-arrow {
  transform: rotate(-135deg);
}
.incr-arrow {
  transform: rotate(45deg);
}
